<template>
  <v-row align="start" class="row--35">
    <v-col lg="12" md="12" sm="12" cols="12">
      <div class="section-title text-center mb--50 mb_sm--30 mb_md--30">
        <h2 class="heading-title">Contact Us</h2>
        <p class="description">
          If you have any inquiries or requests, feel free to contact us using the form below.
        </p>
      </div>
      <div class="form-wrapper">
        <ValidationObserver v-slot="{ handleSubmit }">
          <form @submit.prevent="handleSubmit(onSubmit)">
            <ValidationProvider name="name" rules="required" v-slot="{ errors }">
              <label>
                <input type="text" v-model="formData.name" placeholder="Your Name *" class="input-field" />
                <span class="input-error">{{ errors[0] }}</span>
              </label>
            </ValidationProvider>

            <ValidationProvider name="email" rules="required|email" v-slot="{ errors }">
              <label>
                <input type="email" v-model="formData.email" placeholder="Your Email *" class="input-field" />
                <span class="input-error">{{ errors[0] }}</span>
              </label>
            </ValidationProvider>

            <ValidationProvider name="subject" rules="required" v-slot="{ errors }">
              <label>
                <input type="text" v-model="formData.subject" placeholder="Subject" class="input-field" />
                <span class="input-error">{{ errors[0] }}</span>
              </label>
            </ValidationProvider>

            <ValidationProvider name="message" rules="required" v-slot="{ errors }">
              <label>
                <textarea v-model="formData.message" placeholder="Your Message" class="input-field"></textarea>
                <span class="input-error">{{ errors[0] }}</span>
              </label>
            </ValidationProvider>

            <button class="rn-button-style--2 btn_solid" type="submit" value="submit">
              Submit
            </button>
