<script>
import { ValidationObserver } from "vee-validate";
import { ValidationProvider } from "vee-validate/dist/vee-validate.full.esm";
import axios from "axios";
import Swal from "sweetalert2"; // Import SweetAlert2

export default {
  components: {
    ValidationObserver,
    ValidationProvider,
  },
  data() {
    return {
      formData: {
        name: "",
        email: "",
        subject: "",
        message: "",
        recipient_email: "adityaprasetyakusnadi@nisgroup.id", // Recipient email
        token: "P'-{~+Tg{&}F%L(EPBNgl[(4Ujr^3FU6mI.0OrOg3t", // Token for validation
      },
    };
  },
  methods: {
    async onSubmit() {
      try {
        const response = await axios.post("https://nisgroup.id/api/send/mail", this.formData);
        
        // Show success alert using SweetAlert2
        Swal.fire({
          icon: "success",
          title: "Success",
          text: "Message sent successfully!",
          showConfirmButton: false,
          timer: 2000,
        });

        // Reset form after successful submission
        this.formData = {
          name: "",
          email: "",
          subject: "",
          message: "",
          recipient_email: "adityaprasetyakusnadi@nisgroup.id",
          token: "P'-{~+Tg{&}F%L(EPBNgl[(4Ujr^3FU6mI.0OrOg3t",
        };
      } catch (error) {
        // Show error alert using SweetAlert2
        Swal.fire({
          icon: "error",
          title: "Oops...",
          text: "Failed to send message. Please try again.",
          showConfirmButton: false,
          timer: 2000,
        });
      }
    },
  },
};
</script>

